import React, { Component } from 'react';
import { NavLink, Link } from 'react-router-dom';

export default class Nav extends Component{
    navCollapse(){
        
        this.refs.mobiNav.style.display = "block";   
    }
    render(){
        return(
            <nav className="navbar navbar-default">
                <div className="container">
                <div className="navbar-header">
                    <button type="button" onClick={this.navCollapse.bind(this)}  className="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                    <span className="sr-only">Toggle navigation</span>
                    <span className="icon-bar"></span>
                    <span className="icon-bar"></span>
                    <span className="icon-bar"></span>
                    </button>
                    <Link className="navbar-brand" to="/" >Fulu Online</Link>
                </div>
            
                <div ref="mobiNav" className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
                    
                    <ul className="nav navbar-nav navbar-right text-uppercase">
                        <li><NavLink exact activeClassName="activeNav" to="/">Home</NavLink></li>
                        <li><NavLink activeClassName="activeNav" to="/contact">Contact Me</NavLink></li>
                    </ul>
                </div>
                </div>
            </nav>
        )
    }
}


