import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class Footer extends Component{
    render(){
        const social = this.props.Social.map(
            (network, index) => 
                <div className={"social " + network.name} key={index}>
                    <a href={network.url}><i className={"fa fa-" + network.name}></i></a>
                </div>
        );
        
        return(
            <footer>
                <div className="container">
                    <div className="col-md-6">
                        <Link to="/contact" className="btn text-uppercase">work with me</Link>
                        <div className="copyright">
                            Fulufhelo Mukwevho, copyright &copy; {new Date().getFullYear()}
                        </div>
                    </div>
                    <div className="col-md-6 text-right">
                        <h2 className="text-uppercase">find me in the web</h2>
                        { social }
                    </div>
                </div>
            </footer>
        )
    }
}


