import React, { Component } from 'react';
import './App.css';
import './css/font-awesome.min.css';

import Header from './custom/Header';
import Portfolio from './custom/Portfolio';
import Footer from './custom/Footer';


//import Contact from './custom/contact/Contact';

import State from './custom/state';

class App extends Component {
  constructor(){
    super();
    this.state = State;
  }
  render() {
    return (
      <div className="App">
        <Header />
        <Portfolio portfolio={this.state } />
        <Footer Social={this.state.social} />
      </div>
    );
  }
}

export default App;


