import React, { Component } from 'react';

export default class Portfolio extends Component{
    modal(e){
        let modal = this.refs.overlay;
        modal.setAttribute("class", "modal-overlay fadeIn");
        modal.style.display = "block";
        let logo = e.target.src.slice(26, -4);

        this.refs.actualModal.src = "img/"+logo+"-large.jpg";

    }
    close(){
        let modal = this.refs.overlay;
        modal.style.display = "none";
    }
    render(){
        const sites = this.props.portfolio.siteThumbnails.map(
            (site, index) => <div key={index} className="col-md-3">
                                <a href={site.url}>
                                    <img src={site.img } alt={site.name} />
                                </a>
                             </div> 
        );
        const logos = this.props.portfolio.logoThumbnails.map(
            (logo, index) => <div key={index} ref={index} onClick={this.modal.bind(this)} className="col-md-3">
                                <img src={logo } alt={index} />
                             </div> 
        );
        return(
            <section className="portfolio">
                <div className="container">
                    <div className="portfolio-header text-center">
                        <div className="large-icon">
                            <i className="fa fa-briefcase"></i>
                        </div>
                        <h2 className="text-uppercase">Here is some of my work</h2>
                    </div>
                    <div className="portfolio-content">
                        { sites }
                        { logos }
                    </div>
                </div>
                <div className="modal-overlay" ref="overlay" onClick={this.close.bind(this)} >
                    <div className="logos-close" onClick={this.close.bind(this)}>x</div>
                    <div className="actual-modal">
                        <img src="img/ausar-large.jpg" ref="actualModal" alt="ausar" />
                    </div>
                </div>
            </section>
        )
    }
}


