import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Nav from './Navigation';

export default class Header extends Component{
    render(){
        return (
            <header className="clearfix">
                <Nav />

                <div className="header-content container text-center">
                    <p className="header-text fadeIn">
                        Hi, i am <strong>Fulufhelo Mukwevho</strong><br />
                        A Front End Web Developer<br />
                        From Johannesburg<br />
                    </p>

                    <Link to="/contact" className="btn slideDown">WORK WITH ME</Link>
                </div>
            </header>
        )
    }
}


