import React, { Component } from 'react';
import Nav from '../Navigation';

export default class HeaderContact extends Component{
    render(){
        return(
            <header className="contact-header clearfix">
                <Nav />
                <div className="contact-header-text text-center">
                    <h1 className="text-uppercase fadeIn">get in touch</h1>
                </div>
            </header>
        )
    }
}


